'use client';

import { motion } from 'framer-motion';
import { Button } from '../common/Button';
import { useEffect, useState } from 'react';
import { getCTAData, fallbackContent } from '../../../lib/sanity-utils';

interface CTAData {
  title?: string;
  highlightedWords?: string[];
  description?: string;
  primaryCta?: {
    text: string;
    link: string;
  };
  secondaryCta?: {
    text: string;
    link: string;
  };
}

interface CTASectionProps {
  useSanity?: boolean;
}

export const CTASection = ({ useSanity = true }: CTASectionProps = {}) => {
  const [ctaData, setCtaData] = useState<CTAData | null>(null);
  const [isLoading, setIsLoading] = useState(useSanity);

  useEffect(() => {
    if (!useSanity) {
      setIsLoading(false);
      return;
    }

    const fetchCTAData = async () => {
      try {
        const data = await getCTAData();
        setCtaData(data);
      } catch (error) {
        console.error('Error fetching CTA data:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchCTAData();
  }, [useSanity]);

  // Use Sanity data if available, otherwise use fallback
  const content = ctaData || fallbackContent.cta;

  if (isLoading) {
    return (
      <section className="py-20 bg-[#FAFAFA]">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center max-w-4xl mx-auto">
            <div className="h-16 bg-gray-200 rounded animate-pulse mb-6"></div>
            <div className="h-6 bg-gray-200 rounded animate-pulse mb-8 w-3/4 mx-auto"></div>
            <div className="h-12 bg-gray-200 rounded animate-pulse w-48 mx-auto"></div>
          </div>
        </div>
      </section>
    );
  }

  // Helper function to render title with highlighted words
  const renderTitle = (title: string, highlightedWords: string[] = []) => {
    if (!highlightedWords.length) return title;
    
    let processedTitle = title;
    highlightedWords.forEach(word => {
      const regex = new RegExp(`\\b${word}\\b`, 'gi');
      processedTitle = processedTitle.replace(regex, `<span class="text-[#2C9B47] font-serif italic">${word}</span>`);
    });
    
    return processedTitle;
  };
  return (
    <section className="py-20 bg-[#FAFAFA]">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.4 }}
          viewport={{ once: true }}
          className="text-center max-w-4xl mx-auto"
        >
          <h3 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6 leading-tight tracking-tight">
            From{' '}
            <span className="text-[#2C9B47] font-serif italic">Minor Chores</span> to{' '}
            <br />
            <span className="text-[#2C9B47] font-serif italic">Major Opportunities</span>
          </h3>
          <p className="text-xl text-gray-600 mb-8 max-w-3xl mx-auto leading-tight tracking-tight">
            Empowering the next generation by turning minor chores into major opportunities for personal growth and financial success.
          </p>
          <div className="flex justify-center mb-12">
            <Button size="lg" variant="primary">
              Download the App
            </Button>
          </div>
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, delay: 0.6 }}
            viewport={{ once: true }}
            className="w-full max-w-md mx-auto"
          >
            <div className="w-full h-[400px] bg-gradient-to-br from-[#2C9B47]/10 to-[#2C9B47]/5 rounded-2xl flex items-center justify-center border border-[#2C9B47]/20">
              <div className="text-center">
                <div className="w-20 h-20 bg-[#2C9B47]/20 rounded-full flex items-center justify-center mx-auto mb-6">
                  <svg className="w-10 h-10 text-[#2C9B47]" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                  </svg>
                </div>
                <p className="text-[#2C9B47] font-serif italic text-lg">App Preview</p>
                <p className="text-[#2C9B47]/60 text-sm mt-2">Coming soon to iOS and Android</p>
              </div>
            </div>
          </motion.div>
        </motion.div>
      </div>
    </section>
  );
};
