'use client';

import { motion } from 'framer-motion';
import { Accordion } from '../common/Accordion';
import { useEffect, useState } from 'react';
import { getFAQData, fallbackContent } from '../../../lib/sanity-utils';

interface FAQData {
  title?: string;
  highlightedText?: string;
  subtitle?: string;
  faqs?: Array<{
    question: string;
    answer: string;
    order: number;
    isActive: boolean;
  }>;
}

interface FAQSectionProps {
  useSanity?: boolean;
}

export const FAQSection = ({ useSanity = true }: FAQSectionProps = {}) => {
  const [faqData, setFaqData] = useState<FAQData | null>(null);
  const [isLoading, setIsLoading] = useState(useSanity);

  useEffect(() => {
    if (!useSanity) {
      setIsLoading(false);
      return;
    }

    const fetchFAQData = async () => {
      try {
        const data = await getFAQData();
        setFaqData(data);
      } catch (error) {
        console.error('Error fetching FAQ data:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchFAQData();
  }, [useSanity]);

  // Use Sanity data if available, otherwise use fallback
  const content = faqData || fallbackContent;

  if (isLoading) {
    return (
      <section className="py-20 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-start">
            <div className="text-left">
              <div className="h-12 bg-gray-200 rounded animate-pulse mb-6"></div>
              <div className="h-6 bg-gray-200 rounded animate-pulse w-3/4"></div>
            </div>
            <div className="space-y-4">
              {[1, 2, 3, 4, 5, 6].map((i) => (
                <div key={i} className="h-16 bg-gray-200 rounded animate-pulse"></div>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Get FAQs from content or use fallback
  const faqItems = content.faqs?.map((faq, index) => ({
    id: (index + 1).toString(),
    question: faq.question,
    answer: faq.answer
  })) || fallbackContent.faqs.map((faq, index) => ({
    id: (index + 1).toString(),
    question: faq.question,
    answer: faq.answer
  }));

  return (
    <section className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-start">
          {/* Left side - Header */}
          <motion.div
            initial={{ opacity: 0, x: -30 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
            viewport={{ once: true }}
            className="text-left"
          >
            <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6 leading-tight tracking-tight">
              {content.title || "Frequently Asked Questions"}
              {content.highlightedText && (
                <span className="text-[#2C9B47] font-serif italic"> {content.highlightedText}</span>
              )}
            </h2>
            <p className="text-xl text-gray-600 leading-tight tracking-tight">
              {content.subtitle || "Everything you need to know about Minor Chores and how it works for families and young entrepreneurs."}
            </p>
          </motion.div>

          {/* Right side - Accordion */}
          <motion.div
            initial={{ opacity: 0, x: 30 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            viewport={{ once: true }}
          >
            <Accordion items={faqItems} />
          </motion.div>
        </div>
      </div>
    </section>
  );
};
