'use client';

import { motion } from 'framer-motion';
import LogoLoop from '../common/LogoLoop';
import { useEffect, useState } from 'react';
import { getTickerData, fallbackContent } from '../../../lib/sanity-utils';

interface TickerData {
  title?: string;
  logos?: Array<{
    name: string;
    link: string;
  }>;
  speed?: number;
  isActive?: boolean;
}

interface TickerSectionProps {
  useSanity?: boolean;
}

export const TickerSection = ({ useSanity = true }: TickerSectionProps = {}) => {
  const [tickerData, setTickerData] = useState<TickerData | null>(null);
  const [isLoading, setIsLoading] = useState(useSanity);

  useEffect(() => {
    if (!useSanity) {
      setIsLoading(false);
      return;
    }

    const fetchTickerData = async () => {
      try {
        const data = await getTickerData();
        setTickerData(data);
      } catch (error) {
        console.error('Error fetching ticker data:', error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchTickerData();
  }, [useSanity]);

  // Use Sanity data if available, otherwise use fallback
  const content = tickerData || fallbackContent.ticker;

  if (isLoading) {
    return (
      <section className="py-12 bg-[#2C9B47]">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="h-8 bg-[#2C9B47]/20 rounded animate-pulse"></div>
        </div>
      </section>
    );
  }

  if (!content.isActive) {
    return null;
  }
  // Sample company logos - you can replace these with actual company logos
  const companyLogos = [
    {
      node: (
        <div className="text-2xl font-bold text-gray-600">
          Company A
        </div>
      ),
      title: "Company A",
      href: "#"
    },
    {
      node: (
        <div className="text-2xl font-bold text-gray-600">
          Company B
        </div>
      ),
      title: "Company B",
      href: "#"
    },
    {
      node: (
        <div className="text-2xl font-bold text-gray-600">
          Company C
        </div>
      ),
      title: "Company C",
      href: "#"
    },
    {
      node: (
        <div className="text-2xl font-bold text-gray-600">
          Company D
        </div>
      ),
      title: "Company D",
      href: "#"
    },
    {
      node: (
        <div className="text-2xl font-bold text-gray-600">
          Company E
        </div>
      ),
      title: "Company E",
      href: "#"
    },
    {
      node: (
        <div className="text-2xl font-bold text-gray-600">
          Company F
        </div>
      ),
      title: "Company F",
      href: "#"
    }
  ];

  return (
    <section className="py-16 bg-[#2C9B47] border-t border-b border-[#2C9B47]/20">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
          className="text-center mb-8"
        >
          <h3 className="text-lg font-medium text-white/90 mb-2">
          Featured In
          </h3>
        </motion.div>
        
        <div className="h-20 flex items-center">
          <LogoLoop
            logos={companyLogos}
            speed={60}
            direction="left"
            logoHeight={40}
            gap={60}
            pauseOnHover={true}
            fadeOut={true}
            fadeOutColor="#2C9B47"
            scaleOnHover={true}
            ariaLabel="Partner companies"
          />
        </div>
      </div>
    </section>
  );
};
